// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import App from './App';
import axios from 'axios';  //  主要 AJAX 套件
import VueAxios from 'vue-axios';  // 將它轉為 Vue 的套件
import 'bootstrap';
import Loading from 'vue-loading-overlay'; // 讀取效果套件
import 'vue-loading-overlay/dist/vue-loading.css';
import router from './router';
import './bus';
import currencyFilter from './filters/currency';
import VeeValidate from 'vee-validate';
import zhTW from 'vee-validate/dist/locale/zh_tw';
import VueI18n from 'vue-i18n';

//Vue.use(VeeValidate);

Vue.use(VueI18n);

const i18n = new VueI18n({
  locale: 'zhTW'
})
Vue.use(VeeValidate, {
  events: 'input|blur', //這是為了讓使用者離開該欄位時觸發驗證
  i18n,
  dictionary: {
    zhTW
  }
})

Vue.use(VueAxios, axios);  //  加入此行才能執行 axios

axios.defaults.withCredentials = true; //  前端 axios 請求附帶 Cookies 設定 

Vue.config.productionTip = false;

Vue.component('Loading', Loading);  // 執行 效果套件
Vue.filter('currency', currencyFilter);

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>',
  i18n,
})

 router.beforeEach((to, from, next) => { // 驗證登入
 console.log('to', to, 'from', from, 'next', next);
  // ...
  if (to.meta.requiresAuth) {   // 需要驗證的接 api
    const api = 'https://vue-course-api.hexschool.io/api/user/check';
    //const api = `${process.env.APIPATH}/api/user/check` ;
    axios.post(api).then((response) => {  // vue 下才能使用 this
    //console.log(response.data);
    if (response.data.success) {
        next();
    } else {
        next({
        path: '/login',
        });
       }
    });
  } else {   // 不需驗證的，跳轉到 next()
    next();
  }
});


