<template>
<div>
        <loading :active.sync="isLoading"></loading> <!--讀取效果-->
        <div class="row mt-4">
            <div class="col-md-4 mb-4" v-for="item in products" :key="item.id">
                <div class="card border-0 shadow-sm">
                    <div style="height: 150px; background-size: cover; background-position: center"
                    :style="{backgroundImage: `url(${item.imageUrl})`}"  >
                    </div>
                    <div class="card-body">
                    <span class="badge badge-secondary float-right ml-2">{{item.category}}</span>
                    <h5 class="card-title">
                        <a href="#" class="text-dark">{{item.title}}</a>
                    </h5>
                    <p class="card-text">{{item.content}}</p>
                    <div class="d-flex justify-content-between align-items-baseline">
                        <div class="h5" v-if="!item.price">{{item.origin_price}}</div>
                        <del class="h6" v-if="item.price">{{item.origin_price}}</del>
                        <div class="h5" v-if="item.price">{{item.price}}</div>
                    </div>
                    </div>
                    <div class="card-footer d-flex">
                    <button type="button" class="btn btn-outline-secondary btn-sm"
                        @click="getProduct(item.id)">
                        <i class="fas fa-spinner fa-spin" v-if="status.LoadingItem === item.id"></i>
                        查看更多
                    </button>
                    <button type="button" class="btn btn-outline-danger btn-sm ml-auto"
                        @click="addtoCart(item.id)">
                        <i class="fas fa-spinner fa-spin" v-if="status.LoadingItem === item.id"></i>
                        加到購物車
                    </button>
                    </div>
                </div>
            </div>
        </div>
    <!-- cart list-->
    <table class="table">
      <thead>
        <tr>
          <th></th>  
          <th>產品名稱</th>
          <th>數量</th>
          <th>單價</th>
        </tr>
      </thead>
      <tbody>
        <!--key 會產生警告(有 eslint 的話)，因為此時沒有資料，但不影響使用-->
        <tr v-for="(item, key) in carts" :key="item.id"> 
          <td width="20px">
          <button type="button" class="btn btn-outline-danger btn-sm"
          @click="removeCartItem(item.id)">
              <i class="fas fa-trash-alt"></i>
          </button>
          </td>
          <td width="100px">{{item.product.title}}
             <div class="text-success" v-if="item.coupon">
                 已使用優惠券
             </div>
          </td>
          <td width="50px">{{item.qty}}/{{item.product.unit}}</td>
          <td width="50px" align="right">{{ item.total | currency}}</td>
        </tr>
      </tbody>
      <tfoot>
      <tr>
          <td colspan="3" align="right">原價</td>
          <td align="right">{{this.total}}</td>
        </tr>
          <tr v-if="this.final_total !== this.total">  <!-- 有折扣時才出現 -->
          <td colspan="3" align="right" class="text-success">折扣價</td>
          <td align="right" class="text-success">{{this.final_total}}</td>
        </tr>
      </tfoot>
    </table>
        <div class='input-group mb-3 input-group-sm'>
          <input type="text" class="text-right" placeholder="請輸入優惠碼">
            <div>
            <button class="btn btn-lg btn-primary btn-block" type="button"
            @click="addCouponCode">送出</button>
            </div> 
        </div>
    <!-- 送出表單 -->
<div class="my-5 row justify-content-center">
  <form class="col-md-6" @submit.prevent="createOrder">
    <div class="form-group">
      <label for="useremail">Email</label>
      <input type="email" class="form-control" name="email" id="useremail"
        v-validate="'required|email'"
        v-model="form.user.email" placeholder="請輸入 Email" required>
      <span class="text-danger"v-if="errors.has('email')">
          {{errors.first('email')}}
      </span>
    </div>
  
    <div class="form-group">
      <label for="username">收件人姓名</label>
      <input type="text" class="form-control" name="name" id="username"
        :class="{'is-invalid': errors.has('name') }"
        v-model="form.user.name" v-validate="'required'" placeholder="輸入姓名">
      <span class="text-danger" v-if="errors.has('name')">姓名必須輸入</span>
    </div>
  
    <div class="form-group">
      <label for="usertel">收件人電話</label>
      <input type="tel" class="form-control" id="usertel" name="phoneNubmer" v-model="form.user.tel" 
       v-validate="'required'" placeholder="請輸入電話">
       <span class="text-danger" v-if="errors.has('phoneNubmer')">請輸入電話</span>
    </div>
  
    <div class="form-group">
      <label for="useraddress">收件人地址</label>
      <input type="text" class="form-control" name="address" id="useraddress" v-model="form.user.address"
       v-validate="'required'"  placeholder="請輸入地址"> 
      <span class="text-danger" v-if="errors.has('address')">地址欄位不得留空</span>
    </div>
  
    <div class="form-group">
      <label for="comment">留言</label>
      <textarea name="" id="comment" class="form-control" cols="30" rows="10" v-model="form.message"></textarea>
    </div>
    <div class="text-right">
      <button class="btn btn-danger">送出訂單</button>
    </div>
  </form>
</div>

    <!-- Modal -->
    <div class="modal fade" id="productModal" tabindex="-1" role="dialog"
       aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div class="modal-dialog modal-lg" role="document">
            <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title" id="exampleModalLabel">{{ product.title }}</h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span> 
                    </button>
                </div>
                <div class="modal-body">
                <img :src="product.image" class="img-fluid" alt="">
                    <blockquote class="blockquote mt-3">
                        <p class="mb-0">{{ product.content }}</p>
                        <footer class="blockquote-footer text-right">{{ product.description }}</footer>
                    </blockquote>
                    <div class="d-flex justify-content-between align-items-baseline">
                        <div class="h4" v-if="!product.price">{{ product.origin_price }} 元</div>
                        <del class="h6" v-if="product.price">原價 {{ product.origin_price }} 元</del>
                        <div class="h4" v-if="product.price">現在只要{{ product.price }} 元</div>
                    </div>
                    <select name="" class="form-control mt-3" v-model="product.num">
                        <option :value="num" v-for="num in 10" :key="num">
                            選購 {{ num }} {{ product.unit }}
                        </option>
                    </select>
                </div>
                <div class="modal-footer">
                    <div class="text-muted text-nowrap mr-3">
                        小計 <strong>{{ product.num * product.price }}</strong> 元
                    </div>
                    <button type="button" class="btn btn-primary"
                    @click="addtoCart(product.id, product.num)">
                    <i class="fas fa-spinner fa-spin" v-if="product.id === status.LoadingItem"></i>
                    加到購物車
                    </button>
                </div>
            </div>
        </div>
    </div>
</div>   
</template>

<script>
import $ from "jquery"; // 因為此元件無法使用 $，所以要先 import 才能使用


 export default {
    data() {
        return {
            products: [],
            product: {},
            carts: {},
            total: 0,
            final_total: 0,
            status: {   // 個別產品讀取效果
                loadingItem: '',
            },
            form: {
                user: {
                    name: '',
                    email: '',
                    tel: '',
                    address: '',
                },
                message: '',
            },
            isLoading: false,
            coupon_code: ''
        };
    },
    methods: {
    getProducts() { 
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products`;
      vm.loading = true;
      this.$http.get(api).then(response => {
      //console.log(response.data);
        vm.products = response.data.products; 
        vm.loading = false;
      });
     },
     getProduct(id) {   // 查看更多
      const vm = this;                                                                     
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/product/${id}`;
      vm.status.loadingItem = id;
      this.$http.get(api).then(response => { 
        //console.log(response);
        vm.product = response.data.product;
        $('#productModal').modal('show');
        vm.status.loadingItem = '';
      }); 
     },
     addtoCart(id, qty = 1) { // api 要傳入兩個參數id, 數量最少是 1
      const vm = this;                                                                     
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      vm.status.loadingItem = id;
      const cart = {
          product_id: id,
          qty,
      };
      this.$http.post(api, {data: cart}).then(response => { 
        //console.log(response);
        vm.status.loadingItem = '';
        vm.getCart();
        $('#productModal').modal('hide');
      }); 
     },
     getCart() {
      const vm = this;                                                                     
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      vm.isLoading = true;
      this.$http.get(api).then(response => { 
      //console.log(response.data.data);
      vm.carts = response.data.data.carts;
      vm.total = response.data.data.total;
      vm.final_total = response.data.data.final_total;
      vm.isLoading = false;
      }); 
     },
     removeCartItem(id) {
      const vm = this;                                                                     
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${id}`;
      vm.isLoading = true;
      this.$http.delete(api).then(response => { 
      //console.log(response);
      //vm.carts = response.data.data.carts;
      this.getCart();
      vm.isLoading = false;
      }); 
     },
     addCouponCode() {
      const vm = this;                                                                     
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/coupon`;
      const coupon = {
          code: vm.coupon_code,
      }
      vm.isLoading = true;
      this.$http.delete(api, { data: coupon }).then(response => { 
      this.getCart();
      vm.isLoading = false;
      }); 
     },
     createOrder() {
      const vm = this;                                                                     
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order`;
      const order = vm.form;
      //vm.isLoading = true;
      this.$validator.validate().then((result) => {  // veevalidate的驗證方法
        if (result) {
            this.$http.post(api, { data: order }).then(response => {  // 自訂義
            console.log('訂單已建立', response);
            if (response.data.success) {
              vm.$router.push(`/customer_checkout/${response.data.orderId}`);
            }  
            //this.getCart();
            vm.isLoading = false;
           }); 
         } else {
             console.log('欄位不完整');
         }
      });
     },
   },       
    created() {
    this.getProducts();
    this.getCart();
   } 
}
</script>

<style scoped>
.table {
  width: 50%;
  /* 置中 */ 
  margin-left:auto;   
  margin-right:auto;
}

.form-coupon {
  width: 30%;
  /* 置中 */ 
  margin-left:auto;   
  margin-right:auto;
}

/* .form-control {
    display: inline-block;
} */

input {
    cursor: pointer;
    border-radius: 5px;
    padding:5px 15px;
    border:1px black solid; 
}

.coupon {
    white-space: nowrap; 
}

th {
  align-content: right;
}

</style>



