import Vue from 'vue'
import Router from 'vue-router'
//import Home from '@/components/HelloWorld'
import dashboard from '@/components/dashboard'
import Login from '@/components/pages/Login'
import Products from '@/components/pages/Products'
import CustomerOrder from '@/components/pages/CustomerOrders'
import CustomerCheckout from '@/components/pages/CustomerCheckout'



Vue.use(Router)  // 啟用

export default new Router({
  routes: [
    {
      path: '*',  //其他不存在的頁面
      redirect: 'Login', //重新導向 login
    },
    // {
    //   path: '/',               // 對應的虛擬路徑
    //   name: 'HelloWorld',      //顯示名稱
    //   component: HelloWorld,   // 對應元件
    //   meta: { requiresAuth: true }, // 路由回傳訊息，判斷登入狀態
    // },
    {
      path: '/login',
      name: 'Login',
      component: Login,
    },
    {
      path: '/admin', 
      name: 'dashboard',
      component: dashboard,
      children: [ // 巢狀 
        {   
        path: 'Products',
        name: 'Products',
        component: Products,
        meta: { requiresAuth: true }, // 路由回傳訊息，判斷登入狀態
      },
     ],
    },
    
    {   // customer_order
      path: '/', 
      name: 'dashboard',
      component: dashboard,
      children: [ // 巢狀 
        {   
        path: 'customer_order',   // url  /#/customer_order
        name: 'CustomerOrder',
        component: CustomerOrder,
        //meta: { requiresAuth: true }, // 路由回傳訊息，判斷登入狀態
      },
        {   
        path: 'customer_checkout/:orderId',   // url  /#/customer_order
        name: 'CustomerCheckout',
        component: CustomerCheckout,
        //meta: { requiresAuth: true }, // 路由回傳訊息，判斷登入狀態
      },
     ],
    },
  ]
});



